function calcAPY(p1,p2,d1,d2){
    var d2 = d2||(new Date()-0)
    var year = 1000*60*60*24*365
    var ratio = p2/p1;
    
    

    return Math.pow(ratio,year/(d2-d1))
}
function dAPY(apy){
    return ((apy-1)*100).toFixed(2)+'%'
}

const BalanceViewer = function(props){

    const config = props.settings;
    const chainInfo = props.chainInfo;

    const { library,account } = useEthers();

    const { error: chefInfoError, value: chefInfo } =
        useCall({
            contract: new Contract(config.chef.address,config.chef.abi),
            method: "userInfo",
            args : [config.chefIndex,account]
        }) ?? {};
    const { error: pendingError, value: pendingHLH } =
        useCall({
            contract: new Contract(config.chef.address,config.chef.abi),
            method: "pendingSushi",
            args : [config.chefIndex,account]
        }) ?? {};
    const { error: emissionsEr, value: emissions } =
        useCall({
            contract: new Contract(config.chef.address,config.chef.abi),
            method: "sushiPerSecond"
        }) ?? {};
    
    console.log('emissions: ',config.chef.address,emissions?ethers.utils.formatEther(emissions[0]):0)
    
    // not fully implemented! needs allocpoints and totalallocpoints!

    //totalAllocPoint
    const { error: apEr, value: totalAllocPoint } =
        useCall({
            contract: new Contract(config.chef.address,config.chef.abi),
            method: "totalAllocPoint"
        }) ?? {};
    
    const { error: poolInfoError, value: poolInfo } =
        useCall({
            contract: new Contract(config.chef.address,config.chef.abi),
            method: "poolInfo",
            args : [config.chefIndex]
        }) ?? {};

    // console.log('PINFO',totalAllocPoint,poolInfo)
    const { error: totalScytheError, value: totalScythe } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: "totalSupply",
        }) ?? {};
    const { error: vaultPPSErr, value: vaultPPS } =
        useCall({
            contract: new Contract(config.vault,config.contract.abi),
            method: "getPricePerFullShare",
        }) ?? {};
    const { error: scythePPSErr, value: scythePPS } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: "getPricePerFullShare",
        }) ?? {};

    const { error: pairSupplyErr, value:  pairSupply } =
        useCall({
            contract: new Contract(config.want,ERC20abi),
            method: "totalSupply",
        }) ?? {};
    
    const USDPair = useTokenBalance(chainInfo.usdc,chainInfo.pool);
    const HLHPair = useTokenBalance(chainInfo.hlh,chainInfo.pool);
    ///const importantBalance = useTokenBalance(config.currencies[config.LPcoins[0]],config.want);
    const firstBalance = useTokenBalance(config.currencies[config.LPcoins[0]],config.want);
    const secondBalance = useTokenBalance(config.currencies[config.LPcoins[1]],config.want);
    
    var HLHPrice = (USDPair&&HLHPair)?(USDPair.mul(ethers.utils.parseEther('1.0')).mul(ethers.utils.parseUnits('1.0',12)).div(HLHPair)):0

    
    const shareBalance = chefInfo?chefInfo[0]:0;
    //const shareBalance = useTokenBalance(config.contract.address, account);
    const [pricePerShare,setPricePerShare] = useState(0);
    const [bApy,setbApy] = useState(0);
    const [sApy,setsApy] = useState(0);
    const signer = library.getSigner();

    const SP = config.symbol==='USD';

   // var per = (firstBalance&&secondBalance&&scythePPS&&vaultPPS&&pairSupply)?(vaultPPS[0].mul(scythePPS[0]).div(ethers.utils.parseEther('1.0')).mul(SP?firstBalance.add(secondBalance):firstBalance).mul(SP?1:2).div(pairSupply[0])):0
    var per = 0;
    //calculate the price per share
    if(firstBalance&&secondBalance&&scythePPS&&vaultPPS&&pairSupply){

        per = scythePPS[0].mul(vaultPPS[0]).div(ethers.utils.parseEther('1.0')); 
        console.log('LP per scythe share: ',ethers.utils.formatEther(per))
        // stable pair (stable to stable)
        if(SP){
            var firstUSD = firstBalance.mul(ethers.utils.parseUnits('1.0',18-(config.tokenDecimals[config.LPcoins[0]]||18)));
            var secondUSD = secondBalance.mul(ethers.utils.parseUnits('1.0',18-(config.tokenDecimals[config.LPcoins[1]]||18)));

            //compsensate for any depegging
            var totalUSD = secondUSD.mul(2);
            if(firstUSD.lt(secondUSD)){
                totalUSD = firstUSD.mul(2);
            }
            
            var LPValue = totalUSD.mul(ethers.utils.parseEther('1.0')).div(pairSupply[0]);
         //   console.log('LP Value: ',ethers.utils.formatEther(LPValue))

            per = per.mul(LPValue).div(ethers.utils.parseEther('1.0'));
            // 
        }else{
            var firstUSD = firstBalance.mul(ethers.utils.parseUnits('1.0',18-(config.tokenDecimals[config.LPcoins[0]]||18)));
            var totalUSD = firstUSD.mul(2);
            var LPValue = totalUSD.mul(ethers.utils.parseEther('1.0')).div(pairSupply[0]);
            per = per.mul(LPValue).div(ethers.utils.parseEther('1.0'));
        }
      //  console.log('USD per scythe share: ',ethers.utils.formatEther(per))

        // if(totalScythe){
        //     console.log('totalScythe: ',ethers.utils.formatEther(totalScythe[0]))
        // }
    }

    if(config.type=='single'){
        per = ethers.utils.parseEther('1.0');
        HLHPrice = ethers.utils.parseEther('1.0');
    }else if(config.LPcoins[0]==='WDOGE'&&HLHPrice){
        HLHPrice = HLHPrice.mul(20); //approximation of DOGE price
    }

    const TVL = (totalScythe&&per)?(totalScythe[0].mul(per).div(ethers.utils.parseEther('1.0'))):(0)
    const TVE = (HLHPrice&&emissions&&poolInfo&&totalAllocPoint)?(HLHPrice.mul(emissions[0].mul(poolInfo.allocPoint).div(totalAllocPoint[0]).mul(60*60*24)).div(ethers.utils.parseEther('1.0'))):0

    


    if(TVL){
        console.log('TVL: ',ethers.utils.formatEther(TVL));
    }




    if(HLHPrice&&emissions){
        console.log('HLH Price: ',ethers.utils.formatEther(HLHPrice))
        console.log('TVE: ',ethers.utils.formatEther(TVE))
    }
    
    // if(scythePPS){
    //     console.log('Vault APY: ',);
    // }
    var vaultAPY = vaultPPS?calcAPY(config.apycheck.vault,ethers.utils.formatEther(vaultPPS[0])-0,config.apyref):1;
    var scytheAPY = scythePPS?calcAPY(config.apycheck.scythe,ethers.utils.formatEther(scythePPS[0])-0,config.apyref):1;

    var emmisionAPY = (TVE&&TVL)?calcAPY(1,(
        ((ethers.utils.formatEther(TVE)-0)+(ethers.utils.formatEther(TVL)-0))/(ethers.utils.formatEther(TVL)-0)
        ),(new Date()-(1000*60*60*24))):1;

    emmisionAPY = Math.min(emmisionAPY,3)
    const stats = [{
        'label':'Shares',
        'value':shareBalance?(formatEther(shareBalance)*config.sharemult).toFixed(3):'--'
    },{
        'label':'Value',
        'value':(shareBalance&&per)?((ethers.utils.formatEther(per.mul(shareBalance).div(ethers.utils.parseEther('1.0')))-0).toFixed(3)+' '+(config.LPcoins[0])):'--',
        'sub':(<i style={{color:'red',position:'absolute'}}>{'+'+((pendingHLH)?((ethers.utils.formatEther(pendingHLH[0])-0).toFixed(3)):('--'))}&nbsp;<img src="/coins/hlh.png" style={{height:'12px'}}/></i>)
    },{
        'label':'est APY',
        'value':dAPY(vaultAPY*scytheAPY*emmisionAPY),
        'color':'#00bcd4',
        'help': (<p>APY Breakdown:<br/>Vault: {dAPY(vaultAPY)}<br/>Reflections: {dAPY(scytheAPY)}<br/>$HLH Rewards: {emmisionAPY==3?('>200%'):dAPY(emmisionAPY)}</p>)
    }]

    var maxlen = 0;
    for(var i=0;i<stats.length;i++){
        if(stats[i].value.length>maxlen){
            maxlen = stats[i].value.length;
        }
    }
    var fontsz = maxlen>=10?18-(maxlen-10)/2:18+'px';

    

    return (
        <div style={{padding:'32px',textAlign:'left',fontWeight:'500'}}>
            <HStack>
                {stats.map(function(stat){
                    return (
                        <StackItem key={stat.label} basis="33%">
                            <StatHolder>
                                <StatLabel><DataTableRow title={stat.label} help={stat.help} /></StatLabel>
                                <StatValueHolder>
                                    <span style={{fontSize:fontsz,color:stat.color||''}}>{stat.value}</span>
                                </StatValueHolder>
                                <>
                                    {stat.sub}
                                </>
                            </StatHolder>
                        </StackItem>
                    )
                })}
                
            </HStack>
        </div>
            
    );
}
export default BalanceViewer;