    "deposit": async function(currency,amount,enter,scytheLock,account,useMin,chainInfo){
      const zapper = new ethers.Contract(this.ZapperHH.address,this.ZapperHH.abi,window.signer);
      const token = new ethers.Contract(this.currencies[currency],ERC20abi,window.signer)

      const inwei = ethers.utils.parseUnits(amount,this.tokenDecimals[currency]||18);

      if(currency===chainInfo.native){
        await zapper.zapInLP(chainInfo.weth,this.vault,parseEther(amount),0,this.contract.address,this.chefIndex,{
          value:parseEther(amount)
        });
      }else{
        const allowance = await token.allowance(account,this.ZapperHH.address)
        

        if(allowance.lt(inwei)){
          window.changeModalPart('(1/2) [approving]');
          const tx = await token.approve(this.ZapperHH.address,parseEther('1000000000'));
          await tx.wait();
          window.changeModalPart('(2/2) [depositing]');
        }

        console.log(this.currencies[currency],this.vault,inwei,0,this.contract.address,this.chefIndex);
        
        await zapper.zapInLP(this.currencies[currency],this.vault,inwei,0,this.contract.address,this.chefIndex);
      }
    },
