Object.keys(stakingOps).forEach((key) => {
  // loop through all stakingOps and apply inheritance (if it has the inherit key then it will inherit from that key)
  if(stakingOps[key].inherit){
    stakingOps[key] = {...stakingOps[stakingOps[key].inherit],...stakingOps[key]}
  }
});
//   // loop through all keys and look for contract properties (properties that start with contract:)
//   Object.keys(stakingOps[key]).forEach((key2) => {
//     if(key2.startsWith('contract:')){
//       // if it is a contract property then we need to load the contract

//     }
//   });
// });
window.stakingOps = stakingOps;
for(var vid in stakingOps){
  var vault = stakingOps[vid];
  vault.contracts = {};
  for(var prop in vault){
    if((typeof(vault[prop])=="string")&&vault[prop].startsWith('contract:')){
      vault.contracts[prop] = vault[prop].split(':')[1];
    }
  }
}
window.stakingOps = stakingOps;

function refreshContracts(chainId){
  for(var vid in stakingOps){
    var vault = stakingOps[vid];
    vault.native = !(chainId === vault.nativeChain || chainId === 1337); // does this vault need to be treated seperatly (read only and data from native chain)
    if(window.livetesting){
      vault.native = true; // require native for live test
    }
    for(var prop in vault.contracts){
      var contractName = vault.contracts[prop];
      var justAddress = false;
      if(contractName.endsWith('-address')){
        contractName = contractName.split('-')[0];
        console.log('just address for ',contractName)
        justAddress = true;
      }
      if(!vault.native){
        stakingOps[vid][prop] = deployments[chainId][0].contracts[contractName];
      }else{
        var nativeChain = vault.nativeChain;
        stakingOps[vid][prop] = deployments[nativeChain][0].contracts[contractName];
      }
      var userSpec = getParameterByName('contract-'+contractName.toLowerCase());
      if(userSpec){
        console.log('replacing contract address for ',contractName,' with ',userSpec);
        stakingOps[vid][prop].address = userSpec;
        stakingOps[vid][prop].userSpec = true;
      }

      if(justAddress){
        stakingOps[vid][prop] = stakingOps[vid][prop].address;
      }
    }
    if(vault.native){
      stakingOps[vid].useChain = vault.nativeChain;
    }else{
      stakingOps[vid].useChain = chainId;
    }

    console.log('refreshed contracts for ',vid,vault.name,' now on chain ',vault.useChain,vault.native ? '[native]' : '[not native]')
  }
}
refreshContracts(window.useChain);

